import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  faJs,
  faReact,
  faNodeJs,
  faHtml5,
  faCss3Alt,
  faGitAlt,
  faPython,
} from '@fortawesome/free-brands-svg-icons'
import { faBrain, faNetworkWired } from '@fortawesome/free-solid-svg-icons'

const skills = [
  { icon: faJs, label: 'JavaScript', color: '#f7df1e' },
  { icon: faReact, label: 'React', color: '#61dafb' },
  { icon: faNodeJs, label: 'Node.js', color: '#3c873a' },
  { icon: faHtml5, label: 'HTML5', color: '#e34c26' },
  { icon: faCss3Alt, label: 'CSS3', color: '#1572b6' },
  { icon: faGitAlt, label: 'Git', color: '#f34f29' },
  { icon: faPython, label: 'Python', color: '#3776AB' },
  { icon: faBrain, label: 'AI / ML', color: '#ffb300' },
  { icon: faNetworkWired, label: 'Active Directory', color: '#00c3ff' },
]

function Skills() {
  return (
    <section id="skills" className="skills-section">
      <h2>Skills</h2>
      <div className="skills-grid">
        {skills.map((skill) => (
          <div className="skill-item" key={skill.label}>
            <span className="skill-icon">
              <FontAwesomeIcon
                icon={skill.icon}
                size="2x"
                color={skill.color}
              />
            </span>
            <span className="skill-label">{skill.label}</span>
          </div>
        ))}
      </div>
    </section>
  )
}

export default Skills
